import home from '../components/home.vue'
import login from '../components/login.vue'

import addComp from '../components/comp/add.vue'
import deleteComp from '../components/comp/delete.vue'
import updateComp from '../components/comp/update.vue'
import addValue from '../components/value/add.vue'
import updateValue from '../components/value/update.vue'
import deleteValue from '../components/value/delete.vue'
import report from '../components/report/projection.vue'
import nullPage from '../components/nullPage'

export default [
    { path: '/', name:'login', component:login },
    { path: '/dashboard', name:'dashboard', component:home  },
    {path:'/components/add', name:'addComp', component: addComp},
    {path:'/components/update', name:'updateComp', component: updateComp},
    {path:'/components/delete', name:'deleteComp', component: deleteComp},
    {path:'/value/add',  name:'addValve', component:addValue},
    {path:'/value/update', name:'upadeValve', component:updateValue},
    {path:'/value/delete', name:'deleteValve', component:deleteValue},
    {path:'/report/project', name:'reportProjection', component:report},
    {path: '*' , component: nullPage}
]