<template>
    <div class="container">
        <br>
        <div class="alert alert-success" role="alert" v-if="noError">
            Component Added Successfully
        </div>
        <br>
        <h1 class=" text-center">Add A Component</h1>
        <div class="container">
            <form>
                <div class="form-group">
                    <label for="cmpName">Component Name</label>
                    <input type="text" class="form-control" id="cmpName" required v-model.trim="newComp.name">
                </div>
                <div class="form-group">
                    <label for="messure">Messurement</label>
                    <input type="text" class="form-control" id="messure" required v-model.trim="newComp.qt">
                </div>
                <button type="submit" class="btn btn-success btn-lg btn-block" @click.prevent="compForm">Add</button>
            </form>
        </div>
        <br><br>
         <div class="container">
        <table class="table">
            <thead class="thead-dark">
                <tr>
                <th scope="col">#</th>
                <th scope="col">Id</th>
                <th scope="col">Name</th>
                <th scope="col">QT</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(comp,index) in comps" :key="index">
                <th scope="row">{{ index+1 }}</th>
                <td>{{comp._id}}</td>
                <td>{{comp.name}}</td>
                <td>{{comp.quantity}}</td>
                </tr>
            </tbody>
        </table>
        <br><br>
    </div>
    </div>
</template>
<script>
import {myMixin} from '../../mixins/mix'
export default {
    mixins:[myMixin]
}
</script>

