<template>
    <div class="container">
        <h1 class=" text-center">Update Component</h1>
        <div class="container">
            <div class="alert alert-success text-center" role="alert" v-if="noError">
            Component Updated Successfully
            </div>
            <br>
            <div class="container">
             <input type="text" class="form-control" placeholder="Filter Components .." v-model="search" @keyup="filteredList">
             <br>
            <table class="table">
                <thead class="thead-dark">
                    <tr>
                    <th scope="col">#</th>
                    <th scope="col">Id</th>
                    <th scope="col">Name</th>
                    <th scope="col">QT</th>
                    <th scope="col">Update</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(comp,index) in compp" :key="index">
                    <th scope="row">{{ index+1 }}</th>
                    <td>{{comp._id}}</td>
                    <td>{{comp.name}}</td>
                    <td>{{comp.quantity}}</td>
                    <td @click.prevent="showComp(comp._id)">
                        <img src="../../assets/pencil-edit.png"/>
                    </td>
                    </tr>
                </tbody>
            </table>
        <br><br>
         </div>
            <form v-if="showUp">
                <div class="form-group">
                    <label for="cmpName">Component Name</label>
                    <input type="text" class="form-control" id="cmpName" required v-model.trim="newComp.name">
                </div>
                <div class="form-group">
                    <label for="messure">Messurement</label>
                    <input type="text" class="form-control" id="messure" required v-model.trim="newComp.qt">
                </div>
                <button type="submit" class="btn btn-warning btn-lg btn-block" @click.prevent="updateComp">Update</button>
            </form>
        </div>
        <br>
    </div>
</template>
<script>
import {myMixin} from '../../mixins/mix'
export default {
   mixins:[myMixin],
   name:"Update",
   data(){
       return{
           filt:[],
       }
   },
   methods:{
       filteredList() {
        this.compp=[]
        this.comps.forEach((comp) => {
            if(comp.name.toLowerCase().includes(this.search.toLowerCase())){
                this.compp.push(comp)
            }
        })
    },
    showComp(compid){
        this.showUp=true;
        this.comps.forEach((comp)=>{
            if(comp._id === compid){
                this.newComp.name = comp.name,
                this.newComp.qt = comp.quantity,
                this.newComp._id = compid
            }
        })
    }
   }
}
</script>

<style scoped>

</style>
