<template>
    <div class="container">
        <h1 class=" text-center">Delete Component</h1>
        <div class="container">
            <div class="alert alert-success" role="alert" v-if="noError">
            Component Deleted Successfully
            </div>
            <form>
                <div class="form-group">
                    <label for="SelectComp">Select Components</label>
                    <select class="form-control" id="SelectComp" v-model="compId">
                        <option v-for="(comp,index) in comps" :key="index" :value="comp._id">{{comp.name}}</option>
                    </select>
                </div>
                <div class="form-group">
                    <label for="cmpName">Component Name</label>
                    <input type="text" readonly class="form-control" id="cmpName" required v-model.trim="newComp.name">
                </div>
                <div class="form-group">
                    <label for="messure">Messurement</label>
                    <input type="text" readonly class="form-control" id="messure" required v-model.trim="newComp.qt">
                </div>
                <button type="submit" class="btn btn-danger btn-lg btn-block" @click.prevent="deletComp">Update</button>
            </form>
        </div>
    </div>
</template>
<script>
import {myMixin} from '../../mixins/mix'
export default {
   mixins:[myMixin],
   name:"Update",
   data(){
       return{
           compId: '',
           compp:[]
       }
   },
   watch:{
        compId() {
            this.comps.forEach((comp)=>{
                if(comp._id === this.compId){
                    this.newComp.name = comp.name,
                    this.newComp.qt = comp.quantity,
                    this.newComp._id = this.compId
                }
            })
        }
   }
}
</script>

